<template>
<div style="height:100%;">
<div class="main-container">
    <div class="main-div">
    <div class="main-content">
            <!-- 状态选择 -->
            <el-select v-model="value" class="for-holder-color" placeholder="全部状态" size="small" style="color:black;float:left;margin:11px;width:120px;">
            <el-option
                   v-for="item in options"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
            </el-option>
            </el-select>

            <!-- 日期选择 -->
            <el-date-picker
                   size="small"
                   style="margin:11px;float:left;"
                   v-model="value6"
                   type="daterange"
                   range-separator="至"
                   start-placeholder="开始日期"
                   end-placeholder="结束日期">
            </el-date-picker>

            <!-- 搜索框 -->
            <el-input placeholder="请输入名称/编码/规格" class="input-search" size="small" style="float:left;">
            <el-button slot="append" icon="el-icon-search" size="mini"></el-button>
            </el-input>

            <!-- 新增 -->
            <el-button class="btn-add" @click="redirectAdd" plain>新增</el-button>

            <!-- 表格数据 -->
            <el-table
                  ref="multipleTable"
                  :data="tableData"
                  :default-sort = "{prop: 'date', order: 'descending'}"
                  tooltip-effect="dark"
                  height="92%"
                  style="width: 100%;"
                  @selection-change="handleSelectionChange">
            
            <el-table-column
                  type="selection"
                  width="30">
            </el-table-column>

            </el-table-column>

            <el-table-column
                  label="图片"
                  width="100">
            <template slot-scope="tableData">
            <div class="table-div-img">
            <img class="table-img" :src="tableData.row.img">
            </div>
            </template>
            </el-table-column>
            <el-table-column
                      prop="name"
                      label="产品名称"
                      width="400">
            </el-table-column>

            <el-table-column
                      prop="status"
                      label="审核状态"
                      width="200">
            </el-table-column>

            <el-table-column
                      prop="date"
                      label="创建时间"
                      show-overflow-tooltip>
            </el-table-column>

            <el-table-column
                      fixed="right"
                      label=""
                      width="100">
            <template slot-scope="scope">
            <el-dropdown trigger="click">
            <span class="el-dropdown-link">
                <i class="el-icon-more"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-on:click.native="redirect(scope.row.id)">新增产品</el-dropdown-item>
            <el-dropdown-item v-on:click.native="open">人工审核</el-dropdown-item>
            <el-dropdown-item v-on:click.native="open2(scope.row.id, scope.$index)">删除</el-dropdown-item>
            </el-dropdown-menu>
            </el-dropdown>
            </template>
            </el-table-column>
            </el-table>

    </div>
    <div>
        <el-pagination
            class="page-div-foot"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[100, 200, 300, 400]"
            :page-size="pageSize"
            layout="jumper, prev, pager, next, sizes, total"
            :total="totalSize">
        </el-pagination>
    </div>
</div>
</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            options: [
            {
                value: '审核通过',
                label: '审核通过',
            },
            {
                value: '审核未通过',
                label: '审核未通过',
            },
            {
                value: '人工审核',
                label: '人工审核',
            },
            ],
            options2: [{
                value: '选项1',
                label: '商品基础信息'
            }],
            value: '',
            value6: '',
            // 表格
            tableData: [
            ],
            multipleSelection: [],
            totalSize:0,
            currentPage: 1,
            pageSize: 100,
        };
    },
    methods:{
        open() {
            this.$confirm('人工审核是否通过', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '审核成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });          
            });
        },
        open2(id, index) {
        let that = this;
            this.$confirm('是否删除', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                console.log(id);
                let url = '/v1/product/checks/'+ id;
                that.$axios.delete(url).then((res) => {
            if(res.data.code == 200){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });          
            });
        },
            redirectAdd(){
                this.$router.push('/product/check/add')
            },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.initTable();
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.initTable();
        },
        initTable() {
            let initList = [];
            let url = "/v1/product/checks?page=" + this.currentPage + "&pagesize=" + this.pageSize;
            let that = this;
            this.$axios.get(url).then((res) => {
                if (res.data.code == 200) {
                    that.totalSize = parseInt(res.data.data.page_total);
                    res.data.data.list.forEach((item) => {
                        let temp = {};
                        temp.id = item.id;
                        temp.name = item.name;
                        temp.date = item.create_time;
                        temp.status = item.status;
                        temp.img = item.img;
                        initList.push(temp);
                    })
                }
                that.tableData = initList;
            }).catch((err) => {
                console.log(err)
            })
        },
        redirect(id) {
            this.$router.push('/product/add?pid='+id);
        }
    },
    created() {
        this.initTable();
    }
}
</script>

<style>
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */
.main-container{
    background-color:white;
    width:100%;   
    height:100%;
}
.main-content{
    padding:10px 30px 0 30px;
    height:91%;
}
.el-col {
  border-radius: 4px;
}
.for-holder-color .el-input__inner::-webkit-input-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-moz-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-ms-input-placeholder {
  color: black;
}
/* 设置table trhover 字体 */
.input-search{
    width:260px;float:left;margin:11px;
}
.high-search{
    float:left;margin-top:10px;font-size:14px;line-height:32px;color:#aaa;
}
.select-c{
    color:black;width:120px;float:left;margin:10px;
}
.btn-add{
    float:right;margin:5px;background-color:#13c19f;color:white;border-color:#13c19f; 
}
.select-out{
    float:right;margin:5px;width:80px;
}
.p-span{
    text-align:left;float:left;font-size:14px;line-height:20px;color:#96a6b6;
}
.p-span-active{
    color:black;
}
.el-table td{
    padding:3px 0;
}
.page-div-foot {
    float:right;margin-top:15px;margin-right:120px;
}
.table-img{
    width:50px;height:50px;border:1px solid #ccc;
}
.table-img:hover{
    width:150px; height:150px;position:absolute;z-index:998;
}
.table-div-img{
    width:50px;height:50px;
}
</style>



